import React from 'react';
import {action} from 'mobx';
import {BaseComponent,Base} from '../../common';
import {Flex,Button,List,Radio,WhiteSpace,WingBlank,NavBar} from 'antd-mobile';

import './AddressManage.less';
import {icon} from '../../images';

const Item = List.Item;
const Brief = Item.Brief;

class AddrItem extends BaseComponent {
    @action.bound
    changeHandler(){
        const {index,callBack} = this.props;
        callBack && callBack(index);
    }
    render(){
        const {name,tel,address,checked} = this.props;
        return(
            <div>
                <List className="addrItem" onClick={this.changeHandler}>
                    <Item
                        multipleLine
                    >
                        {name} <span>{tel}</span>
                        <Brief>{address}</Brief>
                    </Item>
                    <Flex className="addrOpera" justify="between">
	                	<Flex.Item className="defaultAddr"><Radio checked={checked} key={name}></Radio></Flex.Item>
	                	<Flex.Item className="addrEdit"><span></span><span className="spa">|</span><span></span></Flex.Item>
	                </Flex>
                </List>
                <WhiteSpace size="lg" />
            </div>
        )
    }
}

export default class AddressManage extends BaseComponent{
	store={
		curIndex:0,
        addressDate:[{
            name:"",
            tel:"182****3679",
            address:"-- "
        },{
            name:"Kaden McCullough",
            tel:"134****2587",
            address:"-- "
        },{
            name:"",
            tel:"134****2587",
            address:"-- "
        }]
    }
    @action.bound
    changeHandler(index){
        this.store.curIndex = index;
    }
	render(){
		const {curIndex,addressDate} = this.store;
        const addrItem = addressDate.map((item,index)=>{
            return <AddrItem key={index} checked={curIndex === index} index={index} {...item} callBack={this.changeHandler}/>;
        });
		return (
			<div className='AddressManage'>
				<NavBar
                    className="base-line"
                    mode="light"
                    icon={<img src={icon.back} alt=''/>}
                    onLeftClick={Base.goBack}
                    rightContent={<div onClick={()=>Base.push('NewAddress')} className='right-label'></div>}
                ></NavBar>
                <div className="base-content">
                    <div className="SelectAddress-box">
                        {addrItem}
                    </div>
                    <WhiteSpace size="xl" />
                    <WingBlank>
                        <Button type="warning" className="save-address" onClick={()=>Base.push('NewAddress')}>+ </Button>
                    </WingBlank>
                </div>
			</div>
		)
	}
};



