import React from 'react';
// import {action} from 'mobx';
import {BaseComponent,NetImg} from '../../common';
import { WhiteSpace,Carousel} from 'antd-mobile';
import './Hots.less';

import {GoodsList} from '../../components/GoodsList';
import {test} from '../../images';

export class Hots extends BaseComponent{
    store = {
        hotData:[{
            goodsImg:test.test4,
            goodsTit:"THE NORTH FACE 2017",
            goodsPrice:69,
            goodsNum: 69,
        },{
            goodsImg:test.test4,
            goodsTit:"RE:CIPE150/3",
            goodsPrice:69,
            goodsNum: 69,
        },{
            goodsImg:test.test4,
            goodsTit:"Bior   50 2017",
            goodsPrice:69,
            goodsNum: 69,
        },{
            goodsImg:test.test4,
            goodsTit:"MARTINELLI'S  750",
            goodsPrice:69,
            goodsNum: 69,
        }],
    }
    componentDidMount(){
    }
    render(){
        const {hotData} = this.store;
        return (
            <div className='Hots base-content'>
                <Carousel
                    autoplay={true}
                    infinite
                    >
                    {[test.banner1,test.banner1].map(val => (
                        <NetImg
                            key={val}
                            src={val}
                            style={{ width: '100%'}}
                        />
                    ))}
                </Carousel>
                <WhiteSpace size="md" />
                <div className="anchor-recommend"><span></span></div>
                <WhiteSpace size="md" />
                <GoodsList  goodslist={hotData} />
            </div>
        )
    }
}


