import React from 'react';
// import { action } from 'mobx';
import {BaseComponent,Base} from '../../common';
import {Flex,Button,NavBar,SearchBar,Tabs} from 'antd-mobile';
import './MyOrder.less';
import {icon,test} from '../../images';

import {GoodsItem} from '../../components/GoodsItem';
class OrderItem extends BaseComponent{
	render(){
		const renderOrderItem = (this.props.data || []).map((item,key)=>{
			const {orderNum,evaluate,goods,total,storeId,orderType} = item;
			return 	<div className="orderItem" key={key}>
						<Flex justify="between" className="orderItemTit base-line">
							<span>{orderNum}</span>
							<span>{evaluate}</span>
						</Flex>
						{
							(goods || []).map((item,key)=>{
								return <GoodsItem key={key} item={item} />
							})
						}
						<div className="orderItemType">
							<div className="totalItem">1  <span className="priceTotal"> {total}</span></div>
							{
								parseInt(orderType,10) === 1 ? <Flex className='typeBtn' justify='end' align="center">
									<Button type="ghost" inline size="small" className="am-button-borderfix contact"></Button>
									<Button type="ghost" inline size="small" className="am-button-borderfix look-log" onClick={()=>Base.push('ExLog',{storeId})}></Button>
									<Button type="ghost" inline size="small" className="am-button-borderfix eva-order" onClick={()=>Base.push('EvaluateOrder',{storeId})}></Button>
								</Flex> : null
							}
							{
								parseInt(orderType,10) === 2 ? <Flex className='typeBtn' justify='end' align="center">
									<Button type="ghost" inline size="small" className="am-button-borderfix contact"></Button>
								</Flex> : null
							}
							{
								parseInt(orderType,10) === 3 ? <Flex className='typeBtn' justify='end' align="center">
									<Button type="ghost" inline size="small" className="am-button-borderfix look-log" onClick={()=>Base.push('ExLog',{storeId})}></Button>
									<Button type="ghost" inline size="small" className="am-button-borderfix eva-order" onClick={()=>Base.push('EvaluateOrder',{storeId})}></Button>
								</Flex> : null
							}
							{
								parseInt(orderType,10) === 4 ? <Flex className='typeBtn' justify='end' align="center">
									<Button type="ghost" inline size="small" className="am-button-borderfix look-log" onClick={()=>Base.push('ExLog',{storeId})}></Button>
									<Button type="ghost" inline size="small" className="am-button-borderfix eva-order" onClick={()=>Base.push('EvaluateOrder',{storeId})}></Button>
								</Flex> : null
							}
							{
								parseInt(orderType,10) === 5 ? <Flex className='typeBtn' justify='end' align="center">
									<Button type="ghost" inline size="small" className="am-button-borderfix look-log" onClick={()=>Base.push('ExLog',{storeId})}></Button>
									<Button type="ghost" inline size="small" className="am-button-borderfix eva-order" onClick={()=>Base.push('EvaluateOrder',{storeId})}></Button>
								</Flex> : null
							}
							{
								parseInt(orderType,10) === 6 ? <Flex className='typeBtn' justify='end' align="center">
									<Button type="ghost" inline size="small" className="am-button-borderfix look-log" onClick={()=>Base.push('ExLog',{storeId})}></Button>
									<Button type="ghost" inline size="small" className="am-button-borderfix eva-order" onClick={()=>Base.push('EvaluateOrder',{storeId})}></Button>
								</Flex> : null
							}

						</div>
					</div>
		});
		return (
			<div>
				{renderOrderItem}
			</div>
		)
	}
}


export default class MyOrder extends BaseComponent{
	store={
		curIndex:0,
        storeList:[{
            orderNum: '2017072719315492902513994461',
				storeId: '1',
				evaluate:"",
				total:"369",
				orderType:"1",
				goods: [
					{
						img: test.test4,
						title: 'RE:CIPE  150/ 3',
						spec: ' 150ml',
						price: '369',
						goodsId: '1',
						num: 1,
					}
				]
        },{
            orderNum: '2017072719315492902513994461',
			storeId: '2',
			evaluate:"",
			total:"369",
			orderType:"2",
			goods: [
				{
					img: test.test4,
					title: 'RE:CIPE  150/ 3',
					spec: ' 150ml',
					price: '369',
					goodsId: '1',
					num: 1,
				}
			]
        },{
            orderNum: '2017072719315492902513994461',
			storeId: '3',
			evaluate:"",
			total:"369",
			orderType:"3",
			goods: [
				{
					img: test.test4,
					title: 'RE:CIPE  150/ 3',
					spec: ' 150ml',
					price: '369',
					goodsId: '1',
					num: 1,
				}
			]
        },{
            orderNum: '2017072719315492902513994461',
			storeId: '4',
			evaluate:"",
			total:"369",
			orderType:"4",
			goods: [
				{
					img: test.test4,
					title: 'RE:CIPE  150/ 3',
					spec: ' 150ml',
					price: '369',
					goodsId: '1',
					num: 1,
				}
			]
        },{
            orderNum: '2017072719315492902513994461',
			storeId: '5',
			evaluate:"",
			total:"369",
			orderType:"5",
			goods: [
				{
					img: test.test4,
					title: 'RE:CIPE  150/ 3',
					spec: ' 150ml',
					price: '369',
					goodsId: '1',
					num: 1,
				}
			]
        },{
            orderNum: '2017072719315492902513994461',
			storeId: '6',
			evaluate:"",
			total:"369",
			orderType:"6",
			goods: [
				{
					img: test.test4,
					title: 'RE:CIPE  150/ 3',
					spec: ' 150ml',
					price: '369',
					goodsId: '1',
					num: 1,
				}
			]
        }]
    }
	
	renderContent = tab => tab.component;
	render(){
		const {storeList} = this.store;
		const tabs = [
            { title: '', component:<OrderItem data={storeList} /> },
            { title: '', component:<OrderItem data={storeList} />},
            { title: '', component:<OrderItem data={storeList} />},
            { title: '', component:<OrderItem data={storeList} />},
            { title: '', component:<OrderItem data={storeList} />},
            { title: '', component:<OrderItem data={storeList} />},
            { title: '', component:<OrderItem  data={storeList}/>}
        ];
		return (
			<div className='MyOrder'>
				<NavBar
                    className="base-line"
                    mode="light"
                    icon={<img src={icon.back} alt=''/>}
                    onLeftClick={Base.goBack}
                ></NavBar>
                <div className="base-content">
                	<SearchBar placeholder="" maxLength={8} />
                	<Tabs className="nav-tabs" tabs={tabs} initialPage={0}>
                		{this.renderContent}
                    </Tabs>
                </div>
			</div>
		)
	}
};



